import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-approve-finished',
  templateUrl: './approve-finished.component.html',
  styleUrls: ['./approve-finished.component.scss']
})
export class ApproveFinishedComponent implements OnInit {
  tabs = [{
      "url" : "/approve/pending",
      "name" : "我的代办"
  },{
      "url" : "/approve/finished",
      "name" : "我的已办",
      "current" : true
  }]
  
  
  events = [{
    link : "/event"
  }]
  
  constructor() { }

  ngOnInit() {
  }

}



