import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-approve-pending',
  templateUrl: './approve-pending.component.html',
  styleUrls: ['./approve-pending.component.scss']
})
export class ApprovePendingComponent implements OnInit {
  tabs = [{
      "url" : "/approve/pending",
      "name" : "我的代办",
      "current" : true
  },{
      "url" : "/approve/finished",
      "name" : "我的已办"
  }]

  events = [{
    link : "/event"
  },{
    link : "/event"
  }]

  constructor() { }

  ngOnInit() {
  }

}



