import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { BoxComponent } from './box/box.component';
import { ApproveComponent } from './approve/approve.component';
import { ApprovePendingComponent } from './approve-pending/approve-pending.component';
import { ApproveFinishedComponent } from './approve-finished/approve-finished.component';
import { AttenceComponent } from './attence/attence.component';
import { AttenceMineComponent } from './attence-mine/attence-mine.component';
import { AttenceTeamComponent } from './attence-team/attence-team.component';

import { VacationComponent } from './vacation/vacation.component';
import { EventComponent } from './event/event.component';


const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'login', component: LoginComponent },
  { path: 'box', component: BoxComponent },
  { path: 'approve', 
    component: ApproveComponent,
    children: [
        { path: '', component: ApprovePendingComponent },
        { path: 'pending', component: ApprovePendingComponent },
        { path: 'finished', component: ApproveFinishedComponent }
    ]
  },
  { path: 'attence', 
    component: AttenceComponent,
    children: [
        { path: '', component: AttenceMineComponent },
        { path: 'mine', component: AttenceMineComponent },
        { path: 'team', component: AttenceTeamComponent }
    ]
  },
  { path: 'vacation', 
    component: VacationComponent,
  },
  { path: 'event', 
    component: EventComponent,
  }
];

@NgModule({
  exports: [ RouterModule ],
  imports: [ RouterModule.forRoot(routes) ]
})

export class AppRoutingModule {}


